body {
    background-image: url("/images/landscape.jpg");
    background-size: 100%;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
}

.profile-image {
    width: 50px;
    height: 50px;
    border-radius: 40px;
    float: left;
}

.container-xxl{
    margin: 50px auto;
    background-color: white;
    padding: 0px;
    border-radius: 7px;
    height: 650px;
}

.col-md-8 {
    background-color: white;
    border-radius: 7px;
}

.settings-tray{
    background-color: #eee;
    padding: 10px 15px;
    border-radius: 7px;
    display: flow-root;
}

.settings-tray h6{
    margin-left: 10px;
    margin-top: 15px;
    float: left;
}


.settings-tray .no-gutters{
    padding: 0px;
}

.settings-tray--right{
    float: right;
    margin-top: 5px;
    float: right;
}

.settings-tray--right i{
    margin-left: 10px;
    font-size: 25px;
    color: grey;
    transition: 0.3s;
}

#logout-btn:hover{
    color: red !important;
}

.settings-tray--right i:hover{
    cursor: pointer;
    color: #74b9ff;
}

.search-box{
    background: #fafafa;
    padding: 10px 13px;
}

.search-box .input-wrapper{
    background-color: #fff;
    border-radius: 40px;
}

.search-box .input-wrapper i{
    color: grey;
    margin-left: 7px;
    vertical-align: middle;
    font-size: 20px;
}

.search-box input{
    border: none;
    border-radius: 30px;
    width: 80%;
}

input::-webkit-input-placeholder{
    color: #e3e3e3;
    font-weight: 300;
    margin-left: 20px;
}

input::placeholder{
    color: #e3e3e3;
    font-weight: 300;
    margin-left: 20px;
}

/* remove outline in search-box whan writing */
input:focus{
    outline: none;
}

.friends-list{
    height: 529.6px;
    max-height: 529.6px;
    overflow-y: scroll;
}

.friend-drawer{
    padding: 10px 15px;
    display: flex;
    vertical-align: baseline;
    background: #fff;
    transition: 0.3s ease;
}

/* ?? */
.friend-drawer--grey{
    background-color: #eee;
}

.friend-drawer .text{
    margin-left: 10px;
    width: 45%;
}

.friend-drawer h6{
    margin-top: 6px;
    margin-bottom: 0;
}

.friend-drawer .text p{
    margin: 0;
}

.friend-drawer .time{
    color: grey;
}

.friend-drawer--onhover:hover{
    cursor: pointer;
    background-color: #74b9ff;

}

/* text becomes white on hover */
.friend-drawer--onhover:hover p{
    color: white !important;
    
}
.friend-drawer--onhover:hover h6{
    color: white !important;
    
}
.friend-drawer--onhover:hover .time{
    color: white !important;
    
}

/* line seperating conversations */
hr{
    margin: 5px auto;
    width: 60%;
}

.messages-list{
    height: 542.5px;
    max-height: 542.5px;
    overflow-y: scroll;
}

.chat-bubble{
    padding: 10px 13px;
    background-color: #eee;
    margin: 10px 30px;
    border-radius: 9px;
    position: relative;
}

.chat-bubble:after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 20%;
    width: 0;
    height: 60;
    border: 10px solid transparent;
    border-top-color: #eee;
    border-bottom: 0;
    margin-left: -20px;
    margin-bottom: -10px;
}

.chat-bubble--left{
    left: 0;
    border-right-color: #eee;
    border-right: 0;
    margin-right: -20px;
}

.offset-md-9 .chat-bubble{
    background-color: #74b9ff;
    color: white;
}

.offset-md-9 .col-md-3{
    width: 100%;
}

.offset-md-9 .chat-bubble:after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 90%;
    width: 0;
    height: 60;
    border: 10px solid transparent;
    border-top-color: #74b9ff;
    border-bottom: 0;
    margin-left: -20px;
    margin-bottom: -10px;
}

.chat-box-tray{
    background-color: #eee;
    border-radius: 0 0 7px;
    display: inline-flexbox;
}

.chat-box-tray i{
    color: grey;
    margin-left: 40px;
    font-size: 25px;
}

.chat-box-tray i .bi-arrow-right-circle-fill{
    color: grey;
    margin-left: 10px;
    margin-top: 7px;
}

.chat-box-tray i:hover{
    cursor: pointer;
    color: #74b9ff;
}

.chat-box-tray input{
    border: none;
    border-radius: 30px;
    margin-left: 10px;
    width: 85%;
    vertical-align: text-bottom;
}

/* scroll bar style */
/*width*/
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }

